import { FETCH_ALL, CREATE, UPDATE, DELETE, LIKE } from '../constants/actionTypes';

//Las acciones describen que algo pas, pero no especifican cmo cambi el estado de la aplicacin en respuesta. Esto es trabajo de los reducers.
//El reducer es una funcin pura que toma el estado anterior y una accin, y devuelve en nuevo estado.


// reducer(state,action) => new State

export default (postflats = [], action) => {
  switch (action.type) {
    case FETCH_ALL:
      return action.payload;
    case LIKE:
      return postflats.map((flat) => (flat._id === action.payload._id ? action.payload : flat));
    case CREATE:
      return [...postflats, action.payload];  //we have to spread all the posts and add a new post that is store in the action payload
    case UPDATE:
      return postflats.map((flat) => (flat._id === action.payload._id ? action.payload : flat));
    case DELETE:
      return postflats.filter((flat) => flat._id !== action.payload);
    default:
      return postflats;
  }
};

